* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}

body {
  height: 100%;
  background-color: white;
  font-family: 'Monda', sans-serif;
}
.App {
  height: 100vh;
}

.App-header {
  margin: 0 auto;
  height: 100%;
  width: 45em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#quote-box {
  height: 25em;
  width: 45em;
  position: relative;
 
}
#in-text {
  overflow: auto;
  word-break: break-word;
  height: 80%;
}

#quote-box_inner {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 1s ease;
}

#in-text {
  font-size: 26px;
}

#in-author {
  font-size: 16px;
  position: absolute;
  bottom: 2em;
}

#inner-front {
  position: absolute;
  width: 100%;
  padding: 2em;
  border-radius: 4px;
  height: 100%;
  backface-visibility: hidden;
  
}

#inner-front, #inner-back {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.quote-animation-duration {
  animation: quote-opacity 4.2s;
  -webkit-animation: quote-opacity 4.2s;
}



.quote-box_inner-flipped {
  transform: rotateX(180deg);
}

#inner-back {
  position: absolute;
  border-radius: 4px;
  padding: 2em;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transform: rotateX(180deg);
}

#twitter-button__bird{
 position: absolute;
}

#btns {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 2em;
  justify-content: space-between;
  height: 3em;
}

#btns__social{
  display: flex;
  flex-direction: row;
  height: 100%;

}

#button-quote {
  background-color: rgb(119, 177, 169);
  width: 8em;
  border: none;
  padding: 0.6em;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;
}

#button-twitter, #button-vk {
  position: relative;
  margin-right: 0.9em;
  height: 3em;
  width: 3em;
  padding: 0.6em;
  background-color: rgb(231, 76, 60);
  border-radius: 5px;
}
#button-twitter {
  padding: 0.7em;
}



@-webkit-keyframes quote-opacity {
  0% {
    opacity: 0;
  }
  30%{
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


@keyframes quote-opacity {
  from {opacity: 0;}
  to {opacity: 1;}
}

@media screen and (max-width: 730px) {
  .App-header {
    height: 80%;
    width: 85%;
  }
  #quote-box {
    height: 70%;
    width: 100%;
}
}

@media screen and (max-width: 500px) {
  .App {
    display: flex;
    align-items: center;
  }
  .App-header {
    height: 85%;
    width: 94%;
  }
  #quote-box {
    height: 85%;
    width: 100%;
}
#in-text {
  font-size: 35px;
}
}

@media screen and (max-width: 380px), screen and (max-height: 500px) {
  #in-text {
    font-size: 20px;
  }
}